body {
	font-family: sans, sans-serif;
	font-size: 24px;
	font-weight: 400;
	line-height: 1.5;

	color: #c1cada;
	@include r(768) {
		font-size: 16px; } }

h1 {
	font-family: robConLig;
	font-size: 48px;
	font-weight: 400;
	color: white;
	text-transform: uppercase;
	@include r(768) {
		font-size: 32px; } }

.container {
	width: 1130px;
	margin: 0 auto;
	@include r(1400) {
		width: 100%;
		padding: 0 130px; }
	@include r(768) {
		padding: 0;
		width: calc(100% - 40px); } }

#nav {
	position: fixed;
	left: 30px;
	top: 50%;
	transform: translateY(-50%) !important;
	z-index: 10;
	a {
		position: relative;
		display: block;
		width: 20px;
		height: 21px;
		cursor: pointer;
		transition: 0.4s;
		&:after {
			position: absolute;
			top: 0;
			left: 0;
			content: '';
			width: 100%;
			height: 1px;
			background-color: #55555580; } }

	li.active a {
		width: 50px;
		&:after {
			background-color: $gold; } }
	a:hover {
		width: 40px;
		&:after {
			background-color: $gold; } }

	&.is-active {
		display: none; }

	@include r(768) {
		// display: none
		left: 0; } }

.log-in {
	position: relative;
	&:after {
		content: "";
		position: absolute;
		top: 12px;
		left: -30px;
		width: 1px;
		height: 20px;
		background-color: #55555580;
		@include r(768) {
			display: none; } }

	&__link {
		font-size: 14px;
		color: $gray;
		text-decoration: none;
		transition: 0.4s;

		&:hover {
			color: white; } }

	@include r(768) {
		margin-left: auto;
		&__link {
			font-size: 18px;
			line-height: 1.8;
			text-transform: uppercase; } } }

.section {
	position: relative;
	overflow: hidden;
	@include r(1024) {
		padding-top: 90px;
		background-image: $bg-grad; } }

.burger {
	display: flex;
	align-items: center;
	font-size: 16px;
	text-transform: uppercase;
	color: white;
	background-color: transparent;

	&__btn {
		width: 20px;
		margin-right: 8px;

		span {
			transition: 0.3s;
			display: block;
			height: 2px;
			width: 100%;

			background-color: white;
			border-radius: 10px;

			&:not(:last-child) {
				margin-bottom: 5px; } } }

	&.is-active {
		.burger__btn span {
			&:nth-child(1) {
				transform: translateY(7px) rotate(45deg); }

			&:nth-child(2) {
				transform: translateX(100%);
				opacity: 0;
				visibility: hidden; }

			&:nth-child(3) {
				transform: translateY(-7px) rotate(-45deg); } } } }


#page-preloader {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #ffffff;
    z-index: 100; }

#page-preloader .spinner {
    width: 150px;
    height: 150px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: url('../img/loader.gif') no-repeat 50% 50%;
    margin: -16px 0 0 -16px; }
