.btn {
	display: block;
	padding: 18px 23px;

	font-size: 14px;
	font-weight: 600;
	text-align: center;
	color: $gold;
	text-decoration: none;

	background-color: $gold-opac;
	border: 1px solid $gold;
	border-radius: 5px;

	transition: 0.4s;
	@include r(768) {
		padding: 15px 23px; }

	&:hover {
		background-color: $gold-opac-hov; }

	&--white {
		color: white;
		background-color: rgba(255,255,255,0.1);
		border: 1px solid white;

		&:hover {
			background-color: rgba(255,255,255,0.3); } } }
