.first-page {
	background-image: url('../img/first.jpg');
	background-size: cover;
	background-repeat: no-repeat;

	&__scroll {
		position: absolute;
		bottom: 0;
		left: 50%;
		padding-left: 22px;
		padding-top: 4px;
		transform: rotate(-90deg) translate(32%, -50%);

		font-size: 12px;
		color: white;
		text-decoration: none;

		border-top: 1px solid white;
		z-index: 10; }

	&__title {
		position: relative;
		margin-top: 350px;

		font-size: 48px;
		font-weight: 400;
		color: white;
		text-align: center;
		text-transform: uppercase;
		z-index: 10;

		@include r(768) {
			position: absolute;
			top: 35%;
			left: 50%;
			transform: translateX(-50%) !important;

			width: 90%;
			margin-top: 0; } }

	@include r(1024) {
		& {
			background-image: url('../img/first.jpg'); } } }
