.fourth-page {

	&__title {
		position: relative;
		max-width: 575px;
		margin-top: 330px;
		margin-bottom: 35px;
		z-index: 10;
		@include r(1024) {
			margin-top: 50px;
			margin-bottom: 25px; } }

	&__descr {
		position: relative;
		max-width: 575px;
		z-index: 10; }

	&__img {
		display: none;
		position: absolute;
		left: 50%;
		top: 80%;
		transform: translate(-50%, -50%) !important;
		@include r(1024) {
			display: block;
			img {
				width: 200px; } } } }
