.media {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100%;
	@include r(1024) {
		display: none; }

	&__video {
		width: 100%;
		height: 100%; } }
.video-player {
	position: relative; }

.video-player-btn {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 70px;
	height: 70px;
	background-color: rgba(255,255,255,0.3);
	border-radius: 50%;
	@include r(1024) {
		display: none; }

	&:hover {
		.video-player-play {
			background-color: rgba(255,255,255,0.9); } } }

.video-player-play {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	width: 50px;
	height: 50px;
	background-color: rgba(255,255,255,0.2);
	border-radius: 50%;
	box-shadow: 0 0 8px rgba(0,0,0,0.35);
	transition: 0.4s;
	svg {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%); } }



