#nav {
  position: fixed;
  left: 30px;
  top: 50%;
  transform: translateY(-50%) !important;
  z-index: 10;
}

#nav a {
  position: relative;
  display: block;
  width: 20px;
  height: 21px;
  cursor: pointer;
  transition: 0.4s;
}

#nav a:after {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 100%;
  height: 1px;
  background-color: #55555580;
}

#nav li.active a {
  width: 50px;
}

#nav li.active a:after {
  background-color: #fbd664;
}

#nav a:hover {
  width: 40px;
}

#nav a:hover:after {
  background-color: #fbd664;
}

#nav.is-active {
  display: none;
}

@media only screen and (max-width: 768px) {
  #nav {
    left: 0;
  }
}

.log-in {
  position: relative;
}

.log-in:after {
  content: "";
