.burger {
  display: flex;
  align-items: center;
  font-size: 16px;
  text-transform: uppercase;
  color: white;
  background-color: transparent;
}

.burger__btn {
  width: 20px;
  margin-right: 8px;
}

.burger__btn span {
  transition: 0.3s;
  display: block;
  height: 2px;
  width: 100%;
  background-color: white;
  border-radius: 10px;
}

.burger__btn span:not(:last-child) {
  margin-bottom: 5px;
}

.burger.is-active .burger__btn span:nth-child(1) {
  transform: translateY(7px) rotate(45deg);
}

.burger.is-active .burger__btn span:nth-child(2) {
  transform: translateX(100%);
  opacity: 0;
  visibility: hidden;
}

.burger.is-active .burger__btn span:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}

#page-preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #ffffff;
  z-index: 100;
}

#page-preloader .spinner {
  width: 150px;
  height: 150px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: url("../img/loader.gif") no-repeat 50% 50%;
  margin: -16px 0 0 -16px;
}

