.media__video {
  width: 100%;
  height: 100%;
}

.video-player {
  position: relative;
}

.video-player-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70px;
  height: 70px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
}

@media only screen and (max-width: 1024px) {
  .video-player-btn {
    display: none;
  }
}

.video-player-btn:hover .video-player-play {
  background-color: rgba(255, 255, 255, 0.9);
}

.video-player-play {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.35);
  transition: 0.4s;
}

.video-player-play svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
