.sixth-page {

	&__title {
		position: relative;
		margin-top: 160px;
		margin-bottom: 35px;
		text-align: center;
		z-index: 10;
		@include r(1024) {
			margin-top: 100px; }
		@include r(768) {
			margin-top: 20px;
			margin-bottom: 15px; } }
	&__descr {
		position: relative;
		text-align: center;
		z-index: 10; }

	&__img {
		display: none;
		position: absolute;
		left: 50%;
		bottom: 60px;
		transform: translateX(-50%);
		width: 100%;
		img {
			width: 399px;
			height: 438px; }
		@include r(1024) {
			display: flex;
			justify-content: space-between;
			img {
				position: absolute;
				bottom: 0;
				right: 20%;
				width: 250px;
				height: 275px; }
			img:last-child {
				left: 20%; } }

		@include r(768) {
			img {
				right: 0; }
			img:last-child {
				left: 0; } } } }


