.third-page {

	&__title {
		position: relative;
		max-width: 555px;
		margin-top: 360px;
		margin-bottom: 35px;
		z-index: 10;
		@include r(1024) {
			margin-top: 100px; }
		@include r(768) {
			margin-top: 0;
			margin-bottom: 25px; } }

	&__descr {
		position: relative;
		max-width: 555px;
		z-index: 10; }

	&__img {
		display: none;
		position: absolute;
		right: 20%;
		top: 50%;
		transform: translateY(-50%);
		@include r(1024) {
			display: block;
			top: auto;
			right: 50%;
			bottom: 5vh;
			transform: translateX(50%);
			img {
				width: 240px; } } } }
