.header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: baseline;
	padding: 30px 20px 0;

	z-index: 20;

	&__wrapper {
		display: flex;

		@include r(1024) {
			display: none;

			&.is-active {
				display: flex;
				position: absolute;
				top: 0;
				left: 0;

				width: 100%;
				height: 100vh;

				padding: 120px 20px;

				background-image: $bg-grad;
				z-index: 10 !important;
				.header__img {
					display: block; } } } }
	&__img {
		display: none;
		position: absolute;
		bottom: 60px;
		left: 50%;
		transform: translateX(-50%); }

	&__logo {
		position: relative;
		top: 10px;
		font-size: 45px;
		z-index: 20;
		svg {
			fill: #ffffff; }

		@include r(1024) {
			display: none;

			&.is-active {
				display: block;
				position: absolute;
				top: 42px;
				right: 20px; } } }

	&__btn {
		width: 160px; }


	&__burger {
		display: none;
		z-index: 20;
		@include r(1024) {
			display: block; } } }
