.seventh-page {
	.container {
		width: 585px;
		margin: 0;
		margin-left: auto;
		margin-right: 180px;
		@include r(1400) {
			padding: 0; }
		@include r(1400) {
			width: 100%;
			padding: 0 130px; }
		@include r(768) {
			padding: 0;
			margin: 0 auto;
			width: calc(100% - 40px); } }

	&__img {
		display: none;
		position: absolute;
		right: 0;
		@include r(1024) {
			display: block;
			left: 50%;
			bottom: 40px;
			transform: translateX(-50%) !important;
			width: 240px;
			img {
				width: 240px; } } }

	&__title {
		position: relative;
		margin-top: 270px;
		margin-bottom: 35px;
		z-index: 10;
		@include r(1024) {
			margin-top: 100px; }
		@include r(768) {
			margin-top: 20px;
			margin-bottom: 15px; } }

	&__descr {
		position: relative;
		max-width: 575px;
		margin-bottom: 90px;
		z-index: 10;
		@include r(768) {
			margin-bottom: 40px; } }

	&__btn {
		position: relative;
		width: 220px;
		z-index: 10;
		@include r(768) {
			margin: 0 auto; } } }
