.main-nav {
	display: flex;

	&__item {
		margin-right: 55px;
		&:last-child {
			margin-right: 60px; } }

	&__link {
		font-size: 14px;
		color: $gray;
		text-decoration: none;
		transition: 0.4s;

		&:hover {
			color: white; } }

	@include r(768) {
		flex-direction: column;
		&__link {
			font-size: 18px;
			line-height: 1.8;
			text-transform: uppercase; } } }
