.second-page {

	&__img {
		display: none;
		position: absolute;
		bottom: -80%;
		right: 0;
		@include r(1024) {
			display: block;
			bottom: -240px;
			img {
				width: 200px; } } }

	&__title {
		position: relative;
		margin-top: 270px;
		margin-bottom: 35px;
		z-index: 10;
		@include r(1024) {
			margin-top: 100px; }
		@include r(768) {
			margin-top: 40px;
			margin-bottom: 25px; } }

	&__descr {
		position: relative;
		max-width: 575px;
		margin-bottom: 90px;
		z-index: 10;
		@include r(768) {
			margin-bottom: 40px; } }

	&__btn {
		position: relative;
		width: 220px;
		z-index: 10;
		@include r(768) {
			margin: 0 auto; } } }
