.fifth-page {

	&__title {
		position: relative;
		margin-top: 220px;
		margin-bottom: 35px;
		text-align: center;
		z-index: 10;
		@include r(1024) {
			margin-top: 20px; } }

	&__descr {
		position: relative;
		text-align: center;
		z-index: 10; }

	&__img {
		display: none;
		position: absolute;
		left: 50%;
		bottom: 60px;
		transform: translateX(-50%);
		@include r(1024) {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			img {
				width: 300px; } } } }
